import React, { Component } from 'react';
import battery from '../public/battery.png';
import './clock.css';

export function Clock() {
	
    var today = new Date(), time = today.getHours() + ':' + today.getMinutes() + ':' + today.getSeconds();
	var date = today.getFullYear() + '/' + today.getMonth() + '/' + today.getDay();
	
  return (
  
  <div className="timer">
  <img className="Header-logo" src={battery} alt="Battery"/> 
	Current Time : {date + ' ' + time}
  </div>
  );
}